.sidebar-container{position:fixed;left:0;top:6rem;height:100%;width:100%;z-index:1000;background-color:rgba(0,0,0,0.5);transition:all .3s;visibility:visible}.sidebar-container--closed{visibility:hidden}.sidebar{position:fixed;left:0;top:6rem;height:100%;width:25rem;background-color:#2e2e2e;transition:all 0.3s;z-index:1000}.sidebar--closed{left:-25rem}.sidebar__menu-items{display:none}@media only screen and (max-width: 56.25em){.sidebar__menu-items{display:block;margin-bottom:2rem}}.sidebar__account{border-top:1px solid #000}

.order__date-address{display:flex;flex-direction:column}@media only screen and (max-width: 37.5em){.order__date-address{margin-bottom:1rem}}.order__date{display:flex;margin-bottom:1rem}.order__address-container{display:flex;flex-direction:column}.order__address{display:flex;flex-direction:column}.order__total{display:flex}

