@import "../../../sass/base/variables";

.dropdown-menu-alert {
    position: absolute;
    top: 5rem;
    right: 1rem;
    height: auto;
    width: auto;
    background-color: white;
    color: black;
    border: 1px solid $border-color;
    transition: all .3s;
    white-space: nowrap;
    //display: none;
    visibility: hidden;
    opacity: 0;
    cursor: pointer;
    animation: show-alert 4s;

    &__container {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 2rem;
    }
}

.animate-alert{
    animation: show-alert 4s;
}

@keyframes show-alert {
    0% {
        visibility: visible;
        opacity: 1;
    }

    75% {
        visibility: visible;
        opacity: 1;
    }

    100% {
        visibility: hidden;
        opacity: 0;
    }
}


