@import "../../../sass/base/variables";

.dropdown {
    height: 5rem;
    background-color: white;
    border: 1px solid $border-color;
    border-radius: 1rem;
    align-items: center;
    font-size: 1.5rem;
    font-family: inherit;
    width: 100%;
    outline: none;

    &__title {
        margin-left: 2rem;
    }

    &__arrow {
        margin-right: 2rem;
    }
}